import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../components/Home";
import ChildOne from "../components/Child1.vue";
import ChildTwo from "../components/Child2.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/home",
    name: "Home",
    component: Home,
  },

  {
    path: "/child-one",
    name: "ChildOne",
    component: ChildOne,
  },
  {
    path: "/child-two",
    name: "ChildOne",
    component: ChildTwo,
  },
];

export default new VueRouter({
  routes,
  mode: "history",
});
